<template>
    <div>
        <h2>사용자 게시판</h2>
        <div class="container" v-for="(value, index) in data" :key="index" @click="detail(index)">
            <div class="f_wrap">
                <div class="title">{{ value.title }}</div>
                <div class="writer">{{ value.writer }}</div>
            </div>
            <div class="content">{{ value.content }}</div>
        </div>
        <button @click="write">글쓰기</button>
    </div>
  </template>
  
  <script>
  import data from '@/data';
  export default {
    data(){
        return{
            data: data,
        };
    },
    methods:{
        detail(index){
            this.$router.push({
                name: 'Detail',
                params:{
                    contentId:index
                }
            });
        },
        write(){
            this.$router.push({
                path: 'create'
            })
        }
    }
  }
  </script>
  
  <style>

  </style>
  


